

/* 0. Reset ================================= */
@import 'reset';
$accnent-color: #eadbb0;
$orange: #f1a91c; //#e16d3b;
$lightblue: #4f6d7a;
$roboto-font: 'Roboto', sans-serif;

$tablet-bp: 768px;
$desktop-bp: 1280px; 

@mixin tablet  {
	@media (min-width: $tablet-bp) {
		@content;
	}
}

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

html, body {
	width: 100%;
	height: 100%;
}

body {
	font-family: $roboto-font;
	background: #4f6d7a;
}

h1 {
	font-size: 2.2rem;
	padding: 10px 0;
}


/* 2. Content ================================= */

.content {
	position: relative;
	z-index: 5;
	pointer-events: none;
	top: 0%;
	width: 100%;
	padding: 10px;
	padding-top: 65%;
	

	h1 {
		color: $orange;

	}
}

.canvasWrapper {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	overflow: hidden;
	background: $lightblue;
	z-index: 2;
}

#backgroundCanvas {
	width: 100%;
}

.porfolio-wrapper div{
	pointer-events: auto;
	text-align: left;
	width: 100%;
	margin: auto;
	display: flex;
	flex-direction: column;

	@include tablet() {
		width: 50%;
		display: block;
		margin: 20px;

		&:nth-child(odd) {
			margin-left: auto;
		}
	
		&:nth-child(even) {
			margin-right: auto;
		}	
	}
}


.porfolio-wrapper  {
	position: relative;
	
	p {
		font-size: 20px;
		margin-bottom: 20px;
		color: $accnent-color;
		word-wrap: break-word;
		padding: 10px;	
		line-height: 1.2;
		font-weight: 200;
	}
		img {
		width: 100%;
		margin: auto;

		@include tablet() {
			width: 100%;
			margin: 0;
		}
	}

	.slide-out-text {		


		@include tablet() {
			position: absolute;
			top: 3%;
			left: 60%;
			max-width: 40%;
			z-index: -1;
			background-color:rgba(0,0,50, .2);
			padding: 10px; 

		}
		
	}

	.slide-out-text2 {

		@include tablet() {
			position: absolute;
			top: 12.5%;
			left: 4%;
			max-width: 40%;
			z-index: -1;
			background-color:rgba(0,0,50, .2);
			padding: 10px; 

		}
	}

	.slide-out-text3 {

		@include tablet() {
			position: absolute;
			top: 23.5%;
			left: 40%;
			max-width: 40%;
			z-index: -1;
			background-color:rgba(0,0,50, .2);
			padding: 10px; 

		}
	}

	.slide-out-text4 {

		@include tablet() {
			position: absolute;
			top: 34.5%;
			left: 40%;
			max-width: 40%;
			z-index: -1;
			background-color:rgba(0,0,50, .2);
			padding: 10px; 

		}
	}

	.slide-out-text5 {

		@include tablet() {
			position: absolute;
			top: 45.5%;
			left: 4%;
			max-width: 40%;
			z-index: -1;
			background-color:rgba(0,0,50, .2);
			padding: 10px; 

