}
#project1, #project2, #project3, #project4, #project5, #project6  {
	width: 100%;
}


.container {
	width: 100%;
	display: block;
	position: relative;

	.about-me-wrapper {
		z-index: 24;
		display: inline-block;
		padding: 10px;
		background-color:rgba(0,0,50, .2);
		position: relative;
		height: 300px;
		margin: 20px;
		
	
	
			@include tablet() {
				width: 60%;
				padding: 10px;
				margin-left: auto;
				display: block;
			}
	
		h1 {
			margin: 10px;
			border-bottom: 1px solid black;
			@include tablet() {
	
			}
		}
		p {
			display: inline-block;
			text-align: center;
			color: #eadbb0;
			font-size: 15px;
			padding: 10px;
		
			
			@include tablet() {
				display: block;
				font-size: 17px;
				padding: 10px;
				width: 70%;
				text-align: left;
				margin-left: auto;
			}
		}
		img {
			width: 30%;
			display: block;
			margin: auto;
			margin-top: 20px;
		
			@include tablet() {
				width: 35%;
				display: inline-block;
				vertical-align: middle;
				position: absolute;
				top: 25%;
				left: -30%;
				margin-bottom: 20px;
	
			}
		}
	}
}


.extra-work-wrapper {
	z-index: 24;
	padding: 10px;
	background-color:rgba(0,0,50, .2);
	position: relative;
	height: 250px;
	margin-top: 120px;
	width: 100%;
	margin-bottom: 40px;
	margin-right: 10px;


	@include tablet() {
		width: 65%;
		padding: 10px;	
		margin-left: 20px;

	}

	h1 {
		margin: 10px;
		border-bottom: 1px solid black;
		@include tablet() {

		}
	}

	.extra-wrapper {
		display: inline-block;
		width: 31%;
		margin-left: 2px;
		margin-top: 10px;
		padding: 5px;


		p{
			color: #eadbb0;
			width: fit-content;
			font-size: 11px;

			@include tablet() {
				font-size: 20px;
			}
		}
		img {
			width: 100%;
		}
	}



}






